.hero {

    height: 85vh;
    padding: 0 var(--x-pad);

    position: relative;

    &__content {
        height: 75%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        @include respond(iphone) {
            height: 65%;
        }
    }

    &__headline {
        font-size: $hero;
        color: var(--white);
        font-weight: 300;
        margin-bottom: 1rem;

        @include respond(phone) {
            font-size: $h1;
        }

        span {
            color: var(--primary);
            font-weight: 400;
        }
    }

    &__subtitle {
        color: var(--white);
        font-size: $h4;

        @include respond(phone) {
            font-size: $h5;
        }
    }


}