.home-page {

    #orlando, #lookup {
        background-color: var(--bg-light);

        @include dark {
            background-color: var(--bg-dark);

            h2 {
                color: var(--white);
            }

        }
    }

    section#lookup {

        @include dark {
            background-color: var(--black);
        }

    }

}