.reviews {

    padding: var(--y-pad) 0;
    position: relative;

    background-color: var(--bg-light-alt);

    @include dark {
        background-color: var(--bg-dark);
    }

    &__title {
        text-align: center;

        @include dark {
            color: var(--white);
        }
    }

    &__carousel {
        padding: 8rem var(--x-pad);

        display: grid;
        grid-column-gap: 6rem;
        grid-auto-flow: column;
        scroll-snap-type: mandatory;

        overflow-x: scroll;
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */

        &::-webkit-scrollbar {
            display: none;
        }

        @include phone {
            grid-column-gap: 4rem;
        }

        .review:last-child {
            margin-right: 100rem;
        }

    }

    &__cta {
        margin-top: 3rem;
        display: flex;
        justify-content: center;
    }

}