.carat-btn {
    display: flex;
    cursor: pointer;

    svg {
        height: 1.6rem;
        width: 1.6rem;
        margin-left: .5rem;
        stroke: currentColor;
        fill: transparent;
    }
}

.btn, .carat-btn {

    --lt-color: currentColor;
    --dk-color: var(--lt-color);

    --lt-color-alt: var(--lt-color);
    --dk-color-alt: var(--lt-color-alt);

    --lt-text: var(--lt-color);
    --dk-text: var(--dk-color);

    --lt-border: transparent;
    --dk-border: var(--lt-border);

    --lt-bg: transparent;
    --dk-bg: var(--lt-bg);

    --lt-opacity: 1;
    --dk-opacity: var(--lt-opacity);

    @include link {

        color: var(--lt-text);
        opacity: var(--lt-opacity);
        border-color: var(--lt-border);
        background-color: var(--lt-bg);

        &:hover { opacity: var(--lt-opacity); }
        &:focus { outline: none; }

        @include dark {
            color: var(--dk-text);
            opacity: var(--dk-opacity);
            border-color: var(--dk-border);
            background-color: var(--dk-bg);
            &:hover { opacity: var(--dk-opacity); }
        }

    }


    // Global Color Themes
    &.primary, &.hover-primary:hover {
        --lt-color: var(--primary);
        --lt-color-alt: var(--white);
        --dk-color-alt: var(--jet);
    }

    &.light, &.hover-light:hover {
        --lt-color: var(--white);
        --lt-color-alt: var(--jet);
    }

    &.dark, &.hover-dark:hover {
        --lt-color: var(--jet);
        --lt-color-alt: var(--white);
    }



    // Light Color Themes
    &.lt-primary, &.lt-hover-primary:hover {
        --lt-color: var(--primary);
        --lt-color-alt: var(--white);
    }

    &.lt-light, &.lt-hover-light:hover {
        --lt-color: var(--white);
        --lt-color-alt: var(--jet);
    }

    &.lt-dark, &.lt-hover-dark:hover {
        --lt-color: var(--jet);
        --lt-color-alt: var(--white);
    }

    &.lt-primary-text, &.lt-hover-primary-text:hover {
        --lt-color-alt: var(--primary);
    }



    // Dark Color Themes
    &.dk-primary, &.dk-hover-primary:hover {
        --dk-color: var(--primary);
        --dk-color-alt: var(--jet);
    }

    &.dk-light, &.dk-hover-light:hover {
        --dk-color: var(--white);
        --dk-color-alt: var(--jet);
    }

    &.dk-dark, &.dk-hover-dark:hover {
        --dk-color: var(--jet);
        --dk-color-alt: var(--white);
    }

    &.dk-primary-text, &.dk-hover-primary-text:hover {
        --dk-text: var(--primary);
    }


    // Opacity Hover
    &.opacity-75,
    &.lt-opacity-75,
    &.hover-opacity-75:hover,
    &.lt-hover-opacity-75:hover {
        --lt-opacity: .75;
    }

    &.opacity-50,
    &.lt-opacity-50,
    &.hover-opacity-50:hover,
    &.lt-hover-opacity-50:hover {
        --lt-opacity: .5;
    }
    
    &.dk-opacity-75,
    &.dk-hover-opacity-75:hover {
        --dk-opacity: .75;
    }

    &.dk-opacity-50,
    &.dk-hover-opacity-50:hover {
        --dk-opacity: .5;
    }

    

}


.btn {

    display: block;
    min-width: 20rem;
    width: max-content;
    padding: 1.5rem 3rem;

    text-transform: uppercase;
    letter-spacing: $spacing;
    font-weight: 700;
    font-size: 1.4rem;
    text-align: center;
    line-height: 1.7;

    border-width: 2px;
    border-style: solid;

    cursor: pointer;
    transition: all $dt;


    // Button Modifiers
    &.wide { width: 100%; }

    &.hollow, &.hover-hollow:hover, &.lt-hollow, &.lt-hover-hollow:hover {
        --lt-text: var(--lt-color);
        --lt-bg: transparent;
        --lt-border: currentColor;
    }

    &.dk-hollow, &.dk-hover-hollow:hover {
        --dk-text: var(--dk-color);
        --dk-bg: transparent;
        --dk-border: currentColor;
    }

    &.filled, &.hover-fill:hover {
        --dk-text: var(--dk-color-alt);
        --dk-bg: var(--dk-color);
        --dk-border: var(--dk-color);
    }

    &.filled, &.hover-fill:hover, &.lt-filled, &.lt-hover-fill:hover {
        --lt-bg: var(--lt-color);
        --lt-border: var(--lt-color);
        --lt-text: var(--lt-color-alt);
    }

    &.dk-filled, &.dk-hover-fill:hover {
        --dk-bg: var(--dk-color);
        --dk-border: var(--dk-color);
        --dk-text: var(--dk-color-alt);
    }

}