.multipart {
    padding: var(--y-pad) var(--x-pad);
    position: relative;
    overflow: hidden;


    &__container {

        position: relative;
        max-width: var(--x-max);
        margin: 0 auto;

        .section__content {
            max-width: 60rem;

            h2 {
                color: var(--white);
            }

            p {
                color: rgba(white, .7);

                @include phone {
                    color: rgba(white, .8);
                }
            }
        }

    }

    &__bg {
        position: absolute;
        height: 100%;
        width: 100%;

        top: 0;
        left: 0;

        display: flex;
        align-items: center;

        @include ipadPro {
            display: block;
        }

        picture, img { width: 100%; }

        img {
            @include ipadPro {
                position: relative;
                height: 100%;
                object-fit: cover;
            }
        }

        span {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;

            background-image: linear-gradient(to right, rgba(#76402A, .5), transparent);

            @include dark {
                background-color: rgba(black, .5);
            }

            @include phone {
                background-image: linear-gradient(rgba(#76402A, .5), rgba(#76402A, .5));
                background-color: rgba(black, .5);
            }
        }
    }

}


.multicol {

    @include dark {
        background-color: var(--bg-dark);
    }

    padding: var(--y-pad) var(--x-pad);

    &__container {
        position: relative;
        max-width: var(--x-max);
        margin: 0 auto;

        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 15rem;

        @include dark {
            h2 { color: var(--white); }
        }

        @include ipad {
            grid-template-columns: 1fr;
        }
    }

}

.image-banner {
    position: relative;
    height: 50rem;

    display: flex;
    align-items: center;
    justify-content: center;

    padding: var(--x-pad);
    overflow: hidden;

    

    &__bg {
        
        &, span { @include overlay; }

        display: flex;
        align-items: center;

        span {
            background-color: var(--bg-dark);
            opacity: 0;
            @include dark { opacity: .82; }
        }

        picture, img {
            width: 100%;

            @include ipad {
                height: 100%;
                width: 100%;
                object-fit: cover;
            }
        }

    }

    &__text {
        position: relative;
        font-size: $hero;
        color: var(--jet);
        font-weight: 300;
        text-align: center;
        line-height: 1.4;

        @include dark {
            color: var(--white);
        }

        @include ipad {
            font-size: $h1;
        }

        span {
            color: var(--primary);
            font-weight: 400;
        }
    }

}