.header {

    &__cta {
        position: absolute;
        bottom: -2rem;
        right: var(--x-pad);

        padding: 4rem;

        background-color: var(--primary);

        &, &:link, &:visited {
            color: var(--white);

            svg {
                stroke: currentColor;
                fill: transparent;
            }

            @include dark {
                color: var(--jet);
            }
        }

        &:hover {
            opacity: 1;
            // transform: translateY(-.25rem);
            bottom: -1.5rem;
            box-shadow: var(--window-shadow);
        }

        h6 {
            margin-bottom: 1.5rem;
        }

        p {
            font-size: 1.4rem;
            opacity: .75;
        }
    }

}