.input {

    --input-padding: 3rem;
    --input-border: 2px solid var(--text-light);
    --gap: 2rem;
    --input-border-box: 2px solid currentColor;

    border: var(--input-border-box);
    padding: 1rem  var(--input-padding);
    display: flex;
    align-items: center;

    transition: all $dt;

    @include ipad {
        --input-padding: 2rem;
    }

    @include phone {
        border: none;
        display: block;
        padding: 0;
    }

    @include dark {
        color: var(--white);
    }


    &:focus-within {
        border-color: var(--primary);
    }


    &:focus-within &__icon svg {
        stroke: var(--primary);
    }

    &:focus-within &__field {
        border-color: rgba($primary, .5);
    }


    &__icon {

        svg {
            height: 2.5rem;
            width: 2.5rem;
            stroke: currentColor;
            transition: all $dt;
        }
        padding-right: var(--input-padding);

        @include phone {
            display: none;
        }
        
    }


    &__field {
        display: flex;
        flex-direction: column-reverse;
        flex-grow: 1;
        transition: all $dt;

        @include phone {
            border: var(--input-border-box);
            padding: 1rem  var(--input-padding);
        }

        &:not(:first-child) {
            padding-left: var(--input-padding);
            border-left: var(--input-border);

            @include phone {
                border-left: var(--input-border-box);
            }
        }

        &:not(:last-child) {
            padding-right: var(--input-padding);
            @include phone {
                margin-bottom: var(--gap);
            }
        }

        h6 {
            margin-bottom: .5rem;
            color: var(--text-dark);
            transition: all $dt;

            @include dark {
                color: var(--text-light);
            }
        }

        input {

            border: none;
            width: 100%;
            padding: .5rem 0;

            transition: all $dt;

            background: transparent;

            color: currentColor;

        }

        input:focus + h6 {
            color: var(--primary);
        }


    }

}


.textarea {

    padding: 1rem 3rem;
    border: 2px solid currentColor;

    display: flex;
    flex-direction: column-reverse;

    transition: all $dt;

    @include phone {
        padding: 1rem 2rem;
    }

    @include dark {
        color: var(--white);
    }

    &:focus-within {
        border-color: var(--primary);
    }

    h6 {
        color: var(--text-dark);
        transition: all $dt;
        margin-bottom: .5rem;
        @include dark {
            color: var(--text-light);
        }
    }

    textarea {

        background: transparent;
        border: none;
        padding: .5rem 0;

        color: currentColor;

        width: 100%;
        height: 20rem;

        resize: none;
    }

    textarea:focus + h6 {
        color: var(--primary);
    }

}